.page-member {
  h3{
	  @include font-line(17);
	}
  .login-inner {
    margin-bottom: 30px;
		dt{
		  @include font-line(14);
		}
    dd {
      text-align: center;
      margin: 0;
      padding: 0;
    }
    input {
      width: 20px;
    }
    .autocheck {
      text-align: center;
    }
    .blockBtn {
      text-align: center;
      &__btn {
        width: 100%;
      }
    }
  }
  .merit {
    background: #cdead1;
    margin: 40px auto;
    padding: 20px 10px 49px;
    position: relative;
    &__heading {
      &__inner {
        @include font-line(22.5, 32);
        margin-bottom: -6px;
      }
    }
    &__list {
      margin: 0 auto;
      padding: 0;
      display: flex;
      flex-flow: row wrap;
      justify-content: space-between;
      margin-right: 2px;
      &__item {
        border: 1px solid #9d9d9d;
        box-shadow: 2px 3px 0 0 $color-main;
        background: #fff;
        flex: 0 0 47.655%;
        text-align: center;
        margin: 0 0 35px;
        padding: 0;
        position: relative;
        .number {
          margin: auto;
          font-weight: 700;
          @include font-line(11, 11);
          color: #fff;
          font-family: "M PLUS Rounded 1c";
          width: 49px;
          height: 49px;
          border-radius: 50%;
          background: $color-main;
          position: absolute;
          top: -12px;
          left: 5px;
          z-index: 1;
          display: flex;
          flex-flow: column wrap;
          justify-content: center;
          align-items: center;
          strong {
            @include font-line(22, 22);
          }
        }
        .title {
          margin: 0;
          padding: 0 0 13px;
          font-weight: 700;
          font-family: "M PLUS Rounded 1c";
          @include font-line(13, 20);
          color: #1a8a2b;
          display: flex;
          height: 44px;
          width: 100%;
          justify-content: center;
          align-items: center;
        }
        .point {
          padding: 18px 10.5px 13px;
        }
        .character {
          position: absolute;
          width: 55px;
          height: 50.5px;
          top: -51px;
          right: 0;
        }
      }
    }
    &__button {
      width: 100%;
      margin: 0 auto;
      .button {
        width: 100%;
        @include font-line(14, 14);
      }
    }
  }
  .pageReminder{
    .entry{
      &__ttl{
        @include font-line(16);
      }
    }
    .btnArea{
      text-align: center;
      margin: 16px 20px;
      &__btn{
        width: 100%;
        @include font-line(18);
      }
    }
  }
}
